import React, { useRef, useEffect, useState } from "react";
import { styled } from "@mui/material/styles";
import bgVideo from "../../Assets/Video/pexels-rostislav-uzunov-5680034.mp4";
import Typography from "@mui/material/Typography";

const BackgroundVideoWrapper = styled("div")(({ theme }) => ({
  overflow: "hidden",
  width:"100%",
  height:"100vh",
  display:"flex",

}));

const BackgroundVideo = styled("video")(({ theme }) => ({
  zIndex: -2,
  minWidth: "100%",
  minHeight:"100%",
  willChange:"transform",
  position:"fixed",
  top:0,
  left:0,
}));

const BackgroundVideoOverlay=styled("div")(({active,theme})=>({
    zIndex: -1,
    minWidth: "100%",
    minHeight:"100%",
    willChange:"transform",
    position:"fixed",
    top:0,
    left:0,
    transition:theme.transitions.create(["opacity"]),
    opacity:(active ? 0.8:0.5),
    backgroundColor:"black"

}))

const StyledTypography = styled(Typography)(({theme})=>({
    ...theme.typography.button,
    color:"white",
}))

const Background = () => {

  const videoRef = useRef();

  const [stop,setStop]=useState(false);

  useEffect(() => {
    if ((videoRef || {}).current) videoRef.current.playbackRate = 0.75;
  }, [videoRef]);

  useEffect(()=>{
    if ((videoRef || {}).current) stop ? videoRef.current.pause(): videoRef.current.play();
  },[stop])


  useEffect(()=>{
    const onScroll = () => setStop(window.pageYOffset!=0);
        // clean up code
        window.removeEventListener('scroll', onScroll);
        window.addEventListener('scroll', onScroll, { passive: true });
        return () => window.removeEventListener('scroll', onScroll);
  })


  return (
    <BackgroundVideoWrapper>
      <BackgroundVideo autoPlay muted loop ref={videoRef}>
        <source src={bgVideo} type="video/mp4" />
      </BackgroundVideo>
      <BackgroundVideoOverlay active={stop}/>
      {/* <StyledTypography variant="h1">Łukasz Kubiak Design</StyledTypography> */}
    </BackgroundVideoWrapper>
  );
};

export default Background;
